import {
  ComponentFactory,
  ComponentFactoryResolver,
  ComponentRef,
  Directive,
  EmbeddedViewRef,
  Injector,
  Input,
  Renderer2,
  TemplateRef,
  Type,
  ViewContainerRef
} from '@angular/core';
import { TemplateComponent } from '../components/tree-helisa/template.component';

@Directive({
  selector: '[appTemplateNode]'
})
export class TemplateDirective {
  private componentRef: ComponentRef<TemplateComponent>;
  private content: string | TemplateRef<object> | Type<object>;

  @Input() set appTemplateNode(content: string | TemplateRef<object> | Type<object>) {
    if (content == null) {
      this.vcr.createEmbeddedView(this.templateRef);
    } else {
      this.content = content;
      if (this.componentRef) {
        return;
      }
      const factory: ComponentFactory<TemplateComponent> = this.resolver.resolveComponentFactory(TemplateComponent);
      const injector: Injector = Injector.create({ providers: [] });
      this.componentRef = this.vcr.createComponent(factory, 0, injector, this.generateNgContent());
    }
  }

  constructor(
    private renderer: Renderer2,
    private injector: Injector,
    private resolver: ComponentFactoryResolver,
    private vcr: ViewContainerRef,
    private templateRef: TemplateRef<object>
  ) {}

  private generateNgContent(): HTMLElement[][] {
    if (typeof this.content === 'string') {
      const element: HTMLElement = this.renderer.createText(this.content);
      return [[element]];
    } else if (this.content instanceof TemplateRef) {
      const context: {} = {};
      const viewRef: EmbeddedViewRef<{}> = this.content.createEmbeddedView(context);
      return [viewRef.rootNodes];
    } else {
      const factory: ComponentFactory<object> = this.resolver.resolveComponentFactory(this.content);
      const componentRef: ComponentRef<object> = factory.create(this.injector);
      return [[componentRef.location.nativeElement]];
    }
  }
}
